// import { Route, Router } from 'react-router';
import { Link, withRouter } from "react-router-dom";
import './Header.css';
import "../components/css/theme.css";
import React from 'react';

function Header(props){
    return <>
 <nav className="navbar navbar-expand-lg navbar-light top-bar">
  <div className="container">
    <a className="navbar-brand" href="#">Suresh Ghimire | A freelancer</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0 rightbar">
      <li class={`nav-item  ${ props.location.pathname === "/" ? "active" : ""   }`}  >
      <Link class="nav-link" to="/"> Home</Link></li>
      <li class={`nav-item  ${ props.location.pathname === "/about" ? "active" : "" }`}>
      <Link class="nav-link" to="/about"> About</Link> </li>
        <li  class={`nav-item  ${ props.location.pathname === "/portfolio" ? "active" : "" }`}>
      <Link class="nav-link" to="/portfolio"> Portfolio</Link> </li>
      <li  class={`nav-item  ${ props.location.pathname === "/blog" ? "active" : "" }`}>
      <Link class="nav-link" to="/blog"> Blog</Link> </li>
      <li  class={`nav-item  ${ props.location.pathname === "/contact" ? "active" : "" }`}>
      <Link class="nav-link" to="/contact"> Contact</Link> </li>
      </ul>
      </div>
  </div>
</nav>
    </>
    }
    export default withRouter(Header);