import emailjs from "emailjs-com";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMapMarker,faEnvelope,faAddressBook,faPhone} from "@fortawesome/free-solid-svg-icons";
function Contact(){

    function sendEmail(e){
        e.preventDefault();
        emailjs.sendForm('gmail', 'template_kg5rmmk', e.target, 'user_G123mBz6M8E7htqrwnCgB')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset()
    };

    return <>
    
    <section className="jumbotron text-center">
    <div className="container">
        <h1 className="jumbotron-heading">Feel Free To Contact Me !</h1>
        <p className="lead text-muted mb-0">Let's Work together</p>
    </div>
</section>
<div className="container ">
    <div className="row contactForm">
        <div className="col">
            <div className="card">
                <div className="card-header"><i className="fa fa-envelope"></i> Send us Message !
                </div>
                <div className="card-body">
                    <form onSubmit={sendEmail}>
                        <div className="form-group">
                            <label for="name">Name</label>
                            <input type="text" name="name" className="form-control" id="name" aria-describedby="emailHelp" placeholder="Enter name" required/>
                        </div>
                        <div className="form-group">
                            <label for="email">Email address</label>
                            <input type="email" name="email" className="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" required />
                            <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                        </div>

                        <div className="form-group">
                        <label for="name">Service you want</label>
                        <select id="service" name="service" className="form-control control">

<option selected>--Choose Service you looking for --</option>
<option value="web designing">Web Designing</option>
<option value="web development">Web Development</option>
<option value="wordpress Management">Wordpress Management</option>
<option value="project redesign">Project Redesign</option>
<option value="laravel project">Laravel Projects</option>
<option value="video editing">Video Editing</option>
<option value="others">Others</option>
 </select>
</div>

             <div className="form-group">
                        <label for="message">Message</label>
                       <textarea name="message" className="form-control" id="message" rows="6" required></textarea>
                        </div>
                        <div className="mx-auto">
                        <button type="submit" className="btn contactSubmit text-right">Submit</button></div>
                    </form>
                </div>
            </div>
        </div>
        <div className="col-12 col-sm-4">
            <div className="card bg-light mb-3">
                <div className="card-header text-uppercase"><FontAwesomeIcon icon={faAddressBook} />  Contact Details</div>
                <div className="card-body">
                    <p><FontAwesomeIcon icon={faMapMarker} /> &nbsp; Lamjung,Nepal</p>
                     <p><FontAwesomeIcon icon={faEnvelope} /> &nbsp;: suresh.ghimire35@gmail.com</p>
                    <p><FontAwesomeIcon className="fPhone" icon={faPhone} /> &nbsp; +977-9843367155</p>
                </div>
            </div>
        </div>
    </div>
</div>
        
    </>
    }
    
    export default Contact;